import React from 'react';
import styled from 'styled-components';

// TODO extract to a better place
interface FocusableProps {
  focused: boolean;
  hasFocusedChild?: boolean;
}

export const ShowDescription = styled.div`
  color: white;
  opacity: 0.8;
  font-size: 20px;
  transition: all 0.5s ease-in-out;
  height: 46px;
`;

export const SpotlightContainer = styled.div<FocusableProps>`
  padding: 256px 80px 24px;
  transition: all 0.5s ease-in-out;
  ${({ hasFocusedChild }) =>
    hasFocusedChild &&
    `
    margin-bottom: 48px;
    padding: 384px 80px 48px;
    ${ShowDescription} {
      margin-bottom: 32px;
    }
  `};
`;

export const ShowTitle = styled.h1`
  color: white;
  opacity: 0.9;
  font-size: 48px;
  margin-top: 0;
`;

export const PlayButton = styled.span<FocusableProps>`
  display: inline-block;
  padding: 12px 16px;
  border-radius: 26px;
  color: black;
  background-color: white;
  opacity: 0;
  font-size: 24px;
  ${({ focused }) =>
    focused &&
    `
    opacity: 1;
    box-shadow: 0 0 0 2pt white;
  `}
  transition: all .35s ease-in-out;
`;

const SvgLogo = styled.svg`
  margin-bottom: 8px;
  margin-left: 4px;
  fill: #fff;
  opacity: 0.8;
`;

